:root {
    --prime:#f0fbff;
    --second: #303841;
    --third: #7aa5d2;
    --fourth: #7aa5d2;
    --subtext: #f0fbff;
    --iconsize: 6em;
    --defaultpadding: 20px;
    --imagemargin: 10px 10px 10px 10px;
    --filtertoprime: invert(87%) sepia(22%) saturate(568%) hue-rotate(190deg) brightness(109%) contrast(105%);
    --filtertosecond: invert(18%) sepia(10%) saturate(1047%) hue-rotate(172deg) brightness(99%) contrast(89%);
    --filtertothird: invert(72%) sepia(13%) saturate(1279%) hue-rotate(173deg) brightness(88%) contrast(86%);
    --tinytext: 1em;
    --smalltext: 2.5em;
    --medtext: 3em;
    --largetext: 4em;
    --hugetext: 5em;
    --defaulttrans: .3s;
}

Link {
    text-decoration: none;
}

.home footer {
    grid-area: footer;
    text-align: center;
    background-color: var(--fourth);
    display: grid;
    font-size: var(--tinytext);
    grid-template-rows: .75fr 1fr .75fr;
    padding: var(--defaultpadding);
    
    grid-template-areas: 
        'footertop'
        'footermid'
        'footerbot';
}

.home .footer {
    grid-area: footermid;
}

.page {
    color: var(--prime);
    background-color: var(--second);
    font-family: 'Comfortaa';
    position: relative;
    width: 100vw;
    height: 100vh;
}

.home {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    grid-template-rows: 1fr 1fr .1fr;
    position: relative;
    background-color: var(--second);
    width: 100vw;
    height: 100vh;
    grid-template-areas: 
        'topcont top1 top2'
        'bottomcont moreabout bottom1'
        'footer moreabout bottom1';
}

.home .topcont {
    grid-area: topcont;
    text-align: center;
    display: grid;
    color: var(--first);
    padding: var(--defaultpadding);
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas: 
        't'
        'm'
        'b';
}

.home .topcont h1{
    font-size: var(--largetext);
}
.home .topcont h3{
    font-size: var(--smalltext)
}
.home .topcont h5{
    font-size: var(--tinytext);
}

.home .topcont .mid {
    grid-area: m;
    background-color: var(--second);
}

.home .topcont .bot {
    grid-area: b;
    background-color: var(--second);
}

.home .top1 {
    grid-area: top1;
    background-color: var(--third);
    color: var(--second);
    text-decoration: none;
    padding: var(--defaultpadding);
    transition: var(--defaulttrans);
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: 
        'logo'
        'text';
}

.home .top1 img{
    filter: var(--filtertosecond);
    height: var(--iconsize);
    margin: var(--imagemargin);
}

.home .top2 {
    grid-area: top2;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: 
        'top'
        'bottom';
}

.home .top2 .topLink {
    grid-area: top;
    text-decoration: none;
    transition: var(--defaulttrans);
    padding: var(--defaultpadding);
    color: var(--prime);
    background-color: var(--second);
}

.home .text{
    font-size: var(--smalltext);
}

.home .top2 .topLink img {
    transition: var(--defaulttrans);
    margin: var(--imagemargin);
    filter: var(--filtertoprime);
    height: var(--iconsize);
}


.home .top2 .bottomLink {
    background-color: var(--second);
    grid-area: bottom;
    text-decoration: none;
    transition: var(--defaulttrans);
    padding: var(--defaultpadding);
    color: var(--prime);
}

.home .top2 .bottomLink img {
    transition: var(--defaulttrans);
    margin: var(--imagemargin);
    filter: var(--filtertoprime);
    height: var(--iconsize);
}

.home .bottomcont {
    grid-area: bottomcont;
    text-align: center;
    display: grid;
    font-size: var(--tinytext);
    background-color: var(--second);
    padding: var(--defaultpadding);
    grid-template-rows: .75fr 1fr .75fr;
    grid-template-areas: 
        'contenttop'
        'contentmid'
        'contentbot';
}

.home .bottomcont .content {
    grid-area: contentmid;
    color: var(--subtext);
}


.home .moreabout {
    grid-area: moreabout;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: 
        'about'
        'cv';
}


.home .moreabout .about {
    grid-area: about;
    transition: var(--defaulttrans);
    background-color: var(--second);
    text-decoration: none;
    color: var(--prime);
    padding: var(--defaultpadding);
}

.home .moreabout .about img {
    transition: var(--defaulttrans);
    margin: var(--imagemargin);
    filter: var(--filtertoprime);
    height: var(--iconsize);
}

.home .moreabout .cv {
    grid-area: cv;
    transition: var(--defaulttrans);
    background-color: var(--second);
    text-decoration: none;
    color: var(--prime);
    padding: var(--defaultpadding);
}

.home .moreabout .cv img {
    transition: var(--defaulttrans);
    margin: var(--imagemargin);
    filter: var(--filtertoprime);
    height: var(--iconsize);
}

.home .bottom1 {
    grid-area: bottom1;
    display: grid;
    grid-template-rows: 1fr 1fr;
    background-color: var(--prime);
    color: var(--second);
    padding: var(--defaultpadding);
    transition: var(--defaulttrans);
    text-decoration: none;
}

.home .bottom1 img{
    transition: var(--defaulttrans);
    margin: var(--imagemargin);
    filter: var(--filtertosecond);
    height: var(--iconsize);
}

.projects {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 4em 1fr;
    position: relative;
    width: 100vw;
    height: 100vh;
    grid-template-areas: 
        'linkhome linkcontact linkabout'
        'content content content';
}

.projects .tohome {
    text-decoration: none;
    color: var(--first);
    transition: var(--defaulttrans);
    background-color: var(--second);
    font-size: var(--medtext);
    text-align: center;
}

.projects .tocontact {
    text-decoration: none;
    color: var(--second);
    transition: var(--defaulttrans);
    font-size: var(--medtext);
    background-color: var(--third);
    text-align: center;
}

.projects .toabout {
    text-decoration: none;
    color: var(--first);
    transition: var(--defaulttrans);
    font-size: var(--medtext);
    background-color: var(--second);
    text-align: center;
}

.projects .tohome:hover {
    background-color: var(--prime);
    color: var(--second);
}

.projects .tocontact:hover {
    background-color: var(--second);
    color: var(--third);
}

.projects .toabout:hover {
    background-color: var(--prime);
    color: var(--second);
}

.projects .projectcontent{
    grid-area: content;
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    background-color: var(--second);
    padding: 20px;
}

.projects .projectcontent .project {
    display: grid;
    background-color: var(--prime);
    grid-column: 2/3;
    margin: 20px;
    grid-template-columns: 1fr 5fr;
    grid-template-rows: 1fr 0.5fr;
    background-color: var(--prime);
    grid-template-areas: 
        'p1img p1about'
        'p1links p1about';
}

.projects .projectcontent .projectimage {
    background-color: var(--third);
    padding: 1%;
    
}

.projects .projectcontent .projectimage img{
    grid-area: p1img;
    height: 10em;
    
}

.projects .projectcontent .projectabout {
    padding: 10px 20px 10px 20px;
    color: var(--second);
}

.projects .projectcontent .projectlinks {
    grid-area: p1links;
    display: grid;
    grid-template-columns: 1fr 1fr;
    background-color: var(--first);
    margin: 1em;
    grid-template-areas: 
        'leftlink rightlink';
}

.projects .projectcontent .projectlinks a{
    text-decoration: none;
}

.projects .projectcontent .projectlinks .leftlink{
    background-color: var(--second);
    color: var(--prime);
    text-align: center;
    padding: 15% 5% 15% 5%;
}

.projects .projectcontent .projectlinks .rightlink{
    background-color: var(--second);
    color: var(--prime);
    text-align: center;
    padding: 15% 5% 15% 5%;
}

/* Smartphones (portrait and landscape) ----------- */

@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {
    .home {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr .1fr;
        grid-template-areas: 
            'topcont'
            'bottomcont'
            'top1'
            'top2'
            'moreabout'
            'bottom1'
            'footer';
    }

    :root {
        --iconsize: 5em;
        --defaultpadding: 15px;
        --imagemargin: 5px;
        --tinytext: 0.7em;
        --smalltext: 1.5em;
        --medtext: 2em;
        --largetext: 3em;
        --hugetext: 4em;
    }
}

