/* Desktops and laptops ----------- */

@media only screen 
and (min-width : 1300px){
    .home {
        grid-template-columns: 2fr 1fr 1fr;
        grid-template-rows: 1fr 1fr .1fr;
        grid-template-areas: 
            'topcont top1 top2'
            'bottomcont moreabout bottom1'
            'footer moreabout bottom1';
    }
    :root {
        --iconsize: 8em;
        --defaultpadding: 10px 25px 10px 25px;
        --imagemargin: 10px;
        --tinytext: 1.3em;
        --smalltext: 2em;
        --medtext: 3em;
        --largetext: 4.5em;
        --hugetext: 6em;
    }
    .home .top1:hover {
        background-color: var(--second);
        color: var(--third);
    }
    .home .top1:hover img {
        transition: var(--defaulttrans);
        filter: var(--filtertothird);
    }
    .home .top2 .topLink:hover {
        background-color: var(--prime);
        color: var(--second);
    }
    .home .top2 .topLink:hover img {
        transition: var(--defaulttrans);
        filter: var(--filtertosecond);
    }
    .home .top2 .bottomLink:hover {
        background-color: var(--prime);
        color: var(--second);
    }
    .home .top2 .bottomLink:hover img {
        transition: var(--defaulttrans);
        filter: var(--filtertosecond);
    }
    .home .moreabout .about:hover {
        background-color: var(--prime);
        color: var(--second);
    }
    .home .moreabout .about:hover img {
        transition: var(--defaulttrans);
        filter: var(--filtertosecond);
    }
    .home .moreabout .cv:hover {
        background-color: var(--prime);
        color: var(--second);
        transition: var(--defaulttrans);
    }
    .home .moreabout .cv:hover img{
        transition: var(--defaulttrans);
        filter: var(--filtertosecond);
    }
    .home .bottom1:hover img{
        transition: var(--defaulttrans);
        filter: var(--filtertoprime);
    }
    .home .bottom1:hover {
        background-color: var(--second);
        color: var(--prime);
    }
}

